.doc {
  flex-direction: column;
  margin-bottom: 120px;

  @include xs-block {
    width: 100%;
  }
  @include lg-block {
    width: 1140px;
  }


  .doc__title {
    display: flex;
    flex-direction: column;
   

    @include xs-block {
      // margin-bottom: 30px;
    }
    @include lg-block {
      margin-top: 71px;
      align-items: flex-start;
    }

      h1 {
       font-size: 48px;
       @include xs-block {
        order: 1;
      }
       @include lg-block{
        text-align: left;
        margin-bottom: 30px;
       }

      }


      .comment { 
        font-size: 22px;
        line-height: 34px;
        color: #9297A2;
        font-weight: 300;

        @include xs-block {
          order: 2;
          position: relative;
          top: 0;
          font-size: 16px;
          line-height: 34px;
          margin: 0;
        }
        @include lg-block {
          font-size: 22px;
          margin-bottom: 30px;
        } 
      }
  }

  .cookie_title {
    display: flex;
    flex-direction: column;

    @include xs-block {

      align-items: center;
    }
    @include lg-block {
      margin-top: 71px ;
      align-items: flex-start;
    }

    h1 {
      font-size: 48px;
      @include xs-block {
        text-align: center;
        margin-bottom: 20px;
      }
      @include lg-block {
        text-align: left;
        line-height: 54px;
      }
     }
     .comment { 
      @include xs-block {
        order: 2;
        font-size: 16px;
        line-height: 34px;
        margin: 0;
      }
      @include lg-block {
        font-size: 22px;
        line-height: 34px;
        text-align: left;
        margin-bottom: 20px;
      }
      font-size: 22px;
      line-height: 34px;
      color: #9297A2;
      font-weight: 300;
      
    }
     

  }
  
  .doc__text {
    font-size: 18px;
    line-height: 24px;
    font-weight: 300;
  }

  p {
    margin: 16px 0;
  }

}

.xs {
  @include xs(display, block);
}

.xs_n { 
  @include xs(display, none);
}

