.faq_title {
    margin: 20px 0 37px 0;
    text-align: center;
    font-size: 48px;

    @include xs-block {
        line-height: 48px;
    }
    @include lg-block {
        line-height: 100px;
    }
}

.faq {
    width: 660px;
    h2 {
        font-size: 36px;
        margin-top: 32px;
        margin-bottom: 32px;
        font-weight: 500;
    }

    h3 {
        font-size: 24px;
        margin: 30px 0;
        font-weight: 400;
    }

    p {
        font-size: 18px;
        line-height: 24px;
        font-weight: 300;
    }
    
    a {
        color: #0072FF;
        &:hover {
            text-decoration-line: underline;
        }
    }

    &:last-child {
        margin-bottom: 128px;
    }
}



