                    width: 45%;
                    justify-content: flex-start;
                }
                @include xl-block {
                    width: 540px;
                }
  

                .tag__item {
                  font-size: 18px;
                  font-weight: 400;
                  letter-spacing: -0.434118px;
                  text-align: center;
                  margin: 0 30px 30px 0;
                  background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), #000000;
                  border-radius: 8px;
                  &:last-child {
                    background: none;
                  }

                  @include xs-block {
                    padding: 11px;
                      margin: 0 9px 30px 0;
                    &:nth-child(2) {
                        display: none;
                    }
                    &:nth-child(3) {
                     margin-right: 0px;
                    }
                    &:nth-child(5) {
                        display: none;
                    }
                  }
                  @include sm-block {
                    padding: 11px;
                    margin: 0 9px 30px 0;

                    &:nth-child(2) {
                        display: inherit;
                    }
                    &:nth-child(3) {
                     margin-right: 0px;
                    }
                    &:nth-child(5) {
                        display: inherit;
                    }
                  }
                  @include xl-block {
                    margin: 0 20px 25px 0;
                    &:nth-child(1) {
                        margin: 0 30px 30px 0;
                    }
                    &:nth-child(2) {
                        display: inherit;
                    }
                    &:nth-child(3) {
                        display: inherit;
                        margin: 0 0px 30px 0;
                    }
                  &:nth-child(5) {
                      display: inherit;
                  }
                }
                }
            }
           
        }

        .btn {
            position: relative;
            background: linear-gradient(0deg, #FFFFFF, #FFFFFF);
            border-radius: 40px;
            width: 420px;
            height: 80px;

        @include xs-block {
            height: 120px;
            width: 100%;
            border-radius: 60px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 30px;
            margin-bottom: 160px;
        }
        @include sm-block {
            width: 420px;
            height: 80px;

        }
        @include lg-block {
            width: 43%;
            height: 80px;
            margin-top: 41px; 
        }
        @include xl-block {
            width: 420px;
        }

          a {
            font-size: 26px;
            display: inline-block;
            color: #000;
            transition: all 0.3s;

              @include xs-block {
                position: static;
                text-align: center;
                word-wrap: break-word ;
                line-height: 30px;
                width: 65%;
              }
              @include sm-block {
                line-height: 80px;
                width: 90%;
                letter-spacing: -0.627059px;
              }

              &:hover {
                color: #0072FF;
                transform: scale(1.1);
                left: 9.5%;
                transition: all 0.3s;
            }
          
          }
        }

        .footer_xs_pro {
           @include lg(display, none);
            @include xs-block {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: space-between;

                .pro_socials {
                    display: flex;
                    justify-content: space-between;
                    width: 185px;
                    margin-bottom: 90px;
                    img {
                        width: 60px;
                        height: 60px;
                    }
                }
                .pro_footer_text {
                    display: flex;
                    justify-content: center;
                    flex-wrap: wrap;
                    width: 100%;
                    li {
                        margin: 0 3px 30px 0;
                        a{
                            color: white;;
                            font-size: 14px;
                        }
                    }
    
                }
                .footer_copyright {
                    font-size: 12px;
                    padding-bottom: 30px;
                }  
            }

            @include sm-block {
            
                .pro_footer_text {
                    width: 100%;
                    li {
                        a{
                            font-size: 18px;
                        }
                    }
                 
                }
                .footer_copyright {
                    margin-bottom: 30px;
                }
            }
        }
    }
}

#pro_footer {
    @include lg(display, block);
    @include xs(display, none);
    position: relative;
    color: white;
    .footer_dark {
        .footer_xl {
            position: relative;
            .footer__navigation {
                margin-top: 30px;
            }
            .footer_copyright {
                margin-bottom: 0;
                padding-bottom: 30px;
            }
        }
        .footer__socials {
            height: 36px;
            max-width: 210px;
        a {
            padding: 25px;
        }
        }
        
}

