
.support {
  p {
    margin: 16px 0;
  }
 
  @include xs-block {
    background: none; 
  }

  @include lg-block {
    height: calc(100vh - 210px);
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: no-repeat url("../img/Suport_bg.png");
    background-size: 48% 100%;
    background-position: 100% 0;
  }

  @include xl-block {
    justify-content: space-between;

  }

 
      
    .title {
        font-size: 48px;
      }

    .content {
      display: flex;

      @include xs-block {
        flex-direction: column;
      }
      @include lg-block {
        flex-direction: row;
        justify-content: space-between;
      }
      @include xl-block {
        flex-direction: row;
        justify-content: space-between;
      }
    
        .description {
          @include xs-block {
            width: 100%;
          }
          @include lg-block {
            width: 450px
          }
          @include xl-block {
            width: 480px
          }
        

            .text {
              color: #000;
              font-weight: 300;

              @include xs-block {
                text-align: left;
                margin-bottom: 120px;
              }

              @include lg-block {
                font-size: 16px;
                line-height: 22px;
              }
              @include xl-block {
                font-size: 18px;
                line-height: 24px;
              }
                a {
                  color: #0072FF;
                  &:hover {
                      text-decoration-line: underline;
                  }
              }
            }
          }
      
          .viwe {
            @include xs-block {
              margin:0;
              margin-bottom: 60px;
              width: 100%;
            }
            @include lg-block {
               width: 450px;
             }
            @include xl-block {
             width: 540px;
           }

           .bg_smoll {
            position: absolute;
            background: no-repeat url("../img/svg/Support_bg_xs.svg");
            background-size:  100%;
            width: 100vw;
            height: 480px;
            z-index: -1;
            left: 0%;
            
            @include xs-block {
              top: 908px;
            }

            @include sm-block {
              top: 937px;
            }
            @include md-block {
              top: 840px;
            }

            @include lg-block {
              display: none;
            }
           }

            .suport_list {
              display: flex;
              flex-direction: column;
              align-items: center;
              
              @include lg-block {
              
              }

              .item {
                box-sizing: border-box;
                display: flex;
                flex-direction: column;
                justify-content: center;
                height: 100px;
                border-radius: 28px;
                margin-bottom: 30px;
                padding-left: 25px;
                background: no-repeat url("../img/svg/Suport_link_arr.svg"), #FFFFFF;

                @include xs-block {
                  background-position: 285px center;
                  width: 100%;
                }
                @include sm-block {
                  background-position: 380px center;
                  width: 420px;
                }
                @include lg-block {
                  background-position: 340px center;
                  width: 380px;
                }
                @include xl-block {
                  background-position: 380px center;
                  width: 420px;
                }

                h4 {
                    margin-bottom: 10px;
                }
                p {
                  margin: 0;
                  font-size: 14px;
                  line-height: 22px;
                  color: #9297A2;
                  font-size: 300;
                }
              }
        }
      }
    }
}